@import 'helpers/variables';

.bg-yellow {
    background-color: $link-bgc; }
.bg-blue {
    background-color: #064d83; }
.text-yellow {
    color: $link-bgc; }


body {
    margin: 0;
    padding: 0;
    font-family: $main-font;

    p {
        font-size: $default-fz; }

    a {
        &:hover {
            color: white; }
        &:focus, &:active {
            outline: none;
            color: white; } } }

header {
    font-weight: 700; }

.lotok-header {

    &-topline {
        height: 27px;
        line-height: 27px;
        font-size: 0; }

    &-menuline {
        height: 98px;

        &__logo-link {
            width: 100%;
            display: inline-block;
            vertical-align: middle;
            max-width: 375px;
            min-height: 100px; }

        &__logo-image {
            height: 100px !important; } }

    .header-phone {
        font-weight: 400;
        font-size: 25px;
        display: inline-block;
        vertical-align: middle;
        height: 27px;
        padding: 0;
        color: #fff;
        letter-spacing: 0.5px;
        font-family: $h-font; }

    .lang-link {
        font-family: "PT Sans Narrow";
        letter-spacing: 2px;
        font-size: $default-fz;
        padding: 0;
        display: inline-block;
        height: 27px;
        vertical-align: middle;
        width: 50px;
        text-align: center;
        color: #fff;
        &:hover {
            color: #fff;
            text-decoration: underline; } }

    .lang-link + .active {
        color: #535353;
        background: $link-bgc; }

    .lang-switch {
        margin-left: 50px; } }



.lotok-tenders-h {
    background-color: #064d83;
    color: white;
    padding: 30px 0;

    h2 {
        margin: 0 0 30px 0;
        font-size: 48px;
        color: $link-bgc; } }



.lotok-tenders-t {
    padding: 30px 0px 60px 0;
    &--overflow {
        overflow: scroll; }

    &__accept-offer {
        color: #064d83;
        background-color: $link-bgc;
        border-color: $link-bgc;
        border-radius: 0px;
        text-transform: uppercase;
        padding: 10px;
        -webkit-appearance: none;
        -webkit-border-radius: 0px;
        &:hover {
            background-color: $link-bgc--hover;
            border-color: $link-bgc--hover; }
        &:hover, &:active , &:focus {
            outline: none; } }

    &__icon-hryvna {
        background-image: url('../../images/icon-hryvna.png');
        background-repeat: no-repeat;
        display: inline-block;
        height: 10px;
        background-position: contain;
        background-size: contain;
        width: $default-fz; }

    &__tender-descr {
        max-width: 650px;
        text-align: left !important;
        h4 {
            font-weight: bold; } }

    &__table {
        tr {
            th {
                text-align: center; }
            td {
                text-align: center; } } }

    .table {
        font-size: $default-fz;
        tr {
            margin-bottom: 30px; } }

    .table>tbody>tr>td, .table>tbody>tr>th, .table>tfoot>tr>td, .table>tfoot>tr>th, .table>thead>tr>td, .table>thead>tr>th {
        vertical-align: middle; } }



.lotok-footer {
    color: white;
    font-size: $default-fz;

    &__footer-logo {
        transition: 1s;
        &:hover {
            text-decoration: underline;
            opacity: .65; } }

    .footer-middle {
        padding: 30px 0 60px !important; }

    .footer-block {
        padding: 12.5px 0;
        border-top: 3px solid rgba(218, 239, 247, 0.5); }

    .list-unstyled {
        li {
            margin-bottom: 8px; } }



    .soc-link {
        display: inline-block;
        height: 40px;
        width: 40px;
        line-height: 40px;
        border: 1px solid #fff;
        position: relative;
        border-radius: 50%;
        margin-left: 15px;
        img {
            width: 50%;
            height: 50%;
            position: absolute;
            right: 0;
            left: 0;
            top: 0;
            bottom: 0;
            margin: auto; } } }



.h-flex {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    justify-content: flex-end; }


.section-header {
    font-size: 24px;
    line-height: 1.1;
    font-weight: 700;
    margin-bottom: 30px;
    text-transform: uppercase;
    text-align: center;
    font-size: 36px; }


.h-mb {
    margin-bottom: 25px !important; }

.-unstyle {
    text-decoration: none;
    color: inherit;
    transition: opacity .2s ease;

    &:hover {
        text-decoration: underline;
        opacity: .65; } }
